w {\n  display: flex !important;\n}\n\n.wemix__modal-container .wemix__modal__notice .wemix__modal__flow-wrap {\n  display: flex !important;\n  flex-direction: column !important;\n  align-items: center !important;\n  color: #fff !important;\n  width: 80px !important;\n  word-break: keep-all !important;\n  gap: 10px !important;\n}\n\n.wemix__modal-container\n  .wemix__modal__notice\n  .wemix__modal__flow-wrap\n  .wemix__modal__flow-p {\n  font-size: 11px !important;\n  font-weight: 300 !important;\n  padding: 0 !important;\n  margin: 0 !important;\n  color: #fff !important;\n}\n\n.wemix__modal-container .wemix__modal__notice .wemix__modal__flow-wrap span {\n  font-size: 11px !important;\n  color: #000 !important;\n  text-transform: uppercase !important;\n  font-family: Montserrat, sans-serif !important;\n}\n\n.wemix__modal-container .wemix__modal__notice .wemix__modal__flow-wrap em {\n  font-size: 11px !important;\n  font-style: normal !important;\n  color: #000 !important;\n  padding: 0 0 0 3px !important;\n  font-family: Montserrat, sans-serif !important;\n}\n\n.wemix__modal-container .wemix__modal__notice .wemix__modal__flow-icon {\n  width: 40px !important;\n  height: 40px !important;\n  background: #fff !important;\n  border-radius: 14px !important;\n  display: flex !important;\n  justify-content: center !important;\n  align-items: center !important;\n}\n\n.wemix__modal-container .wemix__modal__notice .wemix__modal__flow-icon.app {\n  background: #000 !important;\n}\n\n.wemix__modal-container\n  .wemix__modal__notice\n  .wemix__modal__flow-icon\n  .wemix__modal__flow-icon--app {\n  width: 22px !important;\n  height: auto !important;\n  margin-left: 3px !important;\n}\n\n.wemix__modal-container\n  .wemix__modal__notice\n  .wemix__modal__flow-icon\n  .wemix__modal__flow-icon--scan {\n  width: 20px !important;\n  height: auto !important;\n}\n\n.wemix__modal-container\n  .wemix__modal__notice\n  .wemix__modal__flow-icon\n  .wemix__modal__flow-icon--auth {\n  width: 