ant;\n    bottom: 0 !important;\n    right: 0 !important;\n    width: calc(100% - 40px) !important;\n    padding: 40px 20px !important;\n    gap: 30px !important;\n  }\n\n  .wemix__modal-container .wemix__modal__notice .wemix__modal__flow-arrow {\n    display: none !important;\n  }\n}\n"],"sourceRoot":""}]);
