const Device = {
    isMobile() {
        var UserAgent = navigator.userAgent;
        console.log("isMobile",(UserAgent.match(/iPhone|iPad|iPod|Android|Windows CE|BlackBerry|Symbian|Windows Phone|webOS|Opera Mini|Opera Mobi|POLARIS|IEMobile|lgtelecom|nokia|SonyEricsson/i) != null || UserAgent.match(/LG|SAMSUNG|Samsung/) != null))
        if (UserAgent.match(/iPhone|iPad|iPod|Android|Windows CE|BlackBerry|Symbian|Windows Phone|webOS|Opera Mini|Opera Mobi|POLARIS|IEMobile|lgtelecom|nokia|SonyEricsson/i) != null || UserAgent.match(/LG|SAMSUNG|Samsung/) != null)
        {            
            return true;
        }else{
            return false;
        }
    },
    isPc() {
        const desktop = "win16|win32|win64|mac|macintel";
        if (desktop.indexOf(navigator.platform.toLowerCase()) > -1) return true;
        else return false;
    }
}

export default Device;