import langEn from './en.json';
import langKo from './ko.json';
import langCn from './cn.json';
import langTw from './tw.json';
const resources = {
    en: {
        translation: langEn
    },
    ko: {
        translation: langKo
    },
    zh:{
        translation: langCn
    },
    tw:{
        translation: langTw
    }
}
let lang = navigator.language||navigator.userLanguage;
if(lang.indexOf("-") !== -1){
    lang = lang.substring(0,lang.indexOf("-"));
}

export default class i18n{
    static locale = lang;
    static t(code){             
        let resource = resources[this.locale];
        if(resource == null)  resource = resources["en"];

        return resource.translation[code];
    }

    static setLocale(lang){
        this.locale = lang
    }
}